import { reqCategoryList, reqFloorList, reqGetBannerList } from "@/api";

// home模块的小仓库
const state = {
    // 三级菜单数据
    categoryList: [],
    // 轮播图数据
    bannerList: [],
    // floor数据
    floorList: [],
};
const mutations = {
    // 接收从actions过来的数据，同时修改state中的数据
    CATEGORYLIST(state, categoryList) {
        state.categoryList = categoryList
    },
    BANNERLIST(state, bannerList) {
        state.bannerList = bannerList
    },
    GETFLOORLIST(state, floorList) {
        state.floorList = floorList
    }
};
const actions = {
    // 请求异步数据，向mutations提交修改申请
    async categoryList({ commit }) {
        let result = await reqCategoryList();
        if (result.code === 200) {
            commit("CATEGORYLIST", result.data);
            console.log("三级菜单数据", result.data);
        }
    },
    // 获取首页轮播图的数据基于mockjs
    async getBannerList({ commit }) {
        let result = await reqGetBannerList();
        if (result.code === 200) {
            commit("BANNERLIST", result.data);
            console.log("轮播图数据", result.data);
        }

    },
    // 获取floor数据
    async getFloorList({ commit }) {
        let result = await reqFloorList();
        if (result.code === 200) {
            // 提交mutations
            commit("GETFLOORLIST", result.data);
            console.log("floor数据", result.data);
        }
    }
};
const getters = {};

export default {
    state,
    mutations,
    actions,
    getters
}