<!-- 三级联动商品分类页面 -->
<template>
    <div>
        <!-- 商品分类导航 -->
        <div class="type-nav">
            <div class="container">
                <div @mouseenter="enter" @mouseleave="leave">
                    <h2 class="all">全部商品分类</h2>

                    <!-- 商品分类列表 -->
                    <transition name="sort">
                        <div class="sort" v-show="show">
                            <div class="all-sort-list2">
                                <!-- 一级分类 -->
                                <div @click="goSearch" class="item" v-for="(c1,index) in categoryList"
                                    :key="c1.catagoryId" @mouseover="changeIndex(index)">
                                    <!-- 当鼠标移到h3上时候触发changeIndex事件 -->
                                    <h3 :class="{curItem:currentIndex==index}">
                                        <a href="#" :style="{color:currentIndex==index?'#fafafa':'#333'}"
                                            :data-categoryName="c1.categoryName"
                                            :data-category1Id="c1.categoryId">{{c1.categoryName}}</a>
                                    </h3>
                                    <!-- 二级分类 -->
                                    <div class="item-list clearfix"
                                        :style="{display:currentIndex==index?'block':'none'}">
                                        <div class="subitem" v-for="(c2,index) in c1.categoryChild" :key=c2.categoryId>
                                            <dl class="fore">
                                                <dt>

                                                    <a href="#" :data-categoryName="c2.categoryName"
                                                        :data-category1Id="c2.categoryId">{{c2.categoryName}}</a>
                                                </dt>
                                                <dd>
                                                    <!-- 三级分类 -->
                                                    <em v-for="(c3,index) in c2.categoryChild" :key="c3.categoryId">
                                                        <a href="#" :data-categoryName="c3.categoryName"
                                                            :data-category1Id="c3.categoryId">{{c3.categoryName}}</a>
                                                    </em>

                                                </dd>
                                            </dl>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </transition>
                </div>
                <nav class="nav">
                    <a href="###">服装城</a>
                    <a href="###">美妆馆</a>
                    <a href="###">尚品汇超市</a>
                    <a href="###">全球购</a>
                    <a href="###">闪购</a>
                    <a href="###">团购</a>
                    <a href="###">有趣</a>
                    <a href="###">秒杀</a>
                </nav>

            </div>
        </div>
    </div>
</template>


<script>
import { mapState } from 'vuex';
import throttle from 'lodash/throttle'
export default {
    name: 'TypeNav',

    mounted() {

        // 若当前路由路径不为/home隐藏分类列表
        if (this.$route.path != '/home') {
            this.show = false;
        }
    },
    data() {
        return {
            currentIndex: -1,
            show: true,
        }
    },
    computed: {
        ...mapState({
            categoryList: state => state.home.categoryList,
        })
    },
    methods: {
        // 三级联动节流函数
        changeIndex: throttle(function (index) {
            // console.log(index);
            this.currentIndex = index;

        }, 50),
        // 鼠标移除时触发
        enter() {
            if (this.$route.path != '/home') {
                this.show = true;
            }
        },
        leave() {
            this.currentIndex = -1;
            if (this.$route.path != '/home') {
                this.show = false;
            }
        },
        //进行路由跳转的函数
        goSearch(event) {
            let element = event.target;
            let { categoryname, category1id, category2id, category3id } = element.dataset;

            if (categoryname) {
                // 准备路由跳转的参数对象
                let location = { name: "search" }
                let query = { categoryName: categoryname }
                // 一定是a标签：一级目录
                if (category1id) {
                    query.category1Id = category1id
                    // 一定是a标签：二级目录
                } else if (category2id) {
                    query.category2Id = category2id
                    // 一定是a标签：三级目录
                } else {
                    query.category3Id = category3id
                }
                // 整理完参数

                // 判断如果search进行路由传参的时候，带params参数，要带上
                if (this.$route.params) {
                    location.params = this.$route.params
                    // 动态给location配置对象添加query属性
                    location.query = query;
                    // 路有跳转
                    this.$router.push(location);
                    console.log(location);
                }


            }
        },
    }
}
</script>


<style scoped lang="less">
.type-nav {
    border-bottom: 2px solid #e1251b;

    .container {
        width: 1200px;
        margin: 0 auto;
        display: flex;
        position: relative;

        .all {
            width: 210px;
            height: 45px;
            background-color: #e1251b;
            line-height: 45px;
            text-align: center;
            color: #fff;
            font-size: 14px;
            font-weight: bold;
        }

        .nav {
            a {
                height: 45px;
                margin: 0 22px;
                line-height: 45px;
                font-size: 16px;
                color: #333;
            }
        }

        .sort {
            position: absolute;
            left: 0;
            top: 45px;
            width: 210px;
            height: 461px;
            position: absolute;
            background: #fafafa;
            z-index: 999;

            .all-sort-list2 {
                .item {
                    h3 {
                        line-height: 27px;
                        font-size: 13px;
                        font-weight: 400;
                        overflow: hidden;
                        padding: 0 20px;
                        margin: 0;

                        a {
                            color: #333;
                        }
                    }

                    .item-list {
                        display: none;
                        position: absolute;
                        width: 734px;
                        min-height: 460px;
                        background: #f7f7f7;
                        left: 210px;
                        border: 1px solid #ddd;
                        top: 0;
                        z-index: 9999 !important;

                        .subitem {
                            float: left;
                            width: 650px;
                            padding: 0 4px 0 8px;

                            dl {
                                border-top: 1px solid #eee;
                                padding: 6px 0;
                                overflow: hidden;
                                zoom: 1;

                                &.fore {
                                    border-top: 0;
                                }

                                dt {
                                    float: left;
                                    width: 54px;
                                    line-height: 22px;
                                    text-align: right;
                                    padding: 3px 6px 0 0;
                                    font-weight: 700;
                                }

                                dd {
                                    float: left;
                                    width: 415px;
                                    padding: 3px 0 0;
                                    overflow: hidden;

                                    em {
                                        float: left;
                                        height: 14px;
                                        line-height: 14px;
                                        padding: 0 8px;
                                        margin-top: 5px;
                                        border-left: 1px solid #ccc;
                                    }
                                }
                            }
                        }
                    }

                    // &表示在父级元素上绑定伪类
                    // &:hover {
                    //     .item-list {
                    //         display: block;
                    //     }
                    // }
                }

                .curItem {
                    background-color: #e1251b;

                }

                .item h3>a {

                    text-decoration: none;
                }

                // css样式给一级分类列表加鼠标聚焦特效
                // .item:hover,
                // .item a:hover {
                //     background-color: #e1251b;
                //     text-decoration: none;
                //     color: #fafafa;
                // }
            }
        }

        .sort-enter {
            height: 0px;
        }

        .sort-enter-to {
            height: 461px;
        }

        .sort-enter-active {
            transition: all .5s;
        }

    }
}
</style>
